import React, {} from 'react';

const WorkList = ({site, active, currentRef, onMouseMove}) => {

  const url = (e) => {
    if(e === ''){
      return alert('오픈 준비중입니다');
    }
    window.open(e);
  }

  return (
    <li className={`${active ? "active" : ""}`} ref={active ? currentRef : null}>
      <a href onClick={() => url(site.link)} rel="noopener noreferrer">
        <figure>
          <img src={site.thum} alt="" />
        </figure>
        <dl>
          <dt>
            <i>{site.year}</i>
            {site.title}
          </dt>
          <dd className="text">
            {site.description.map((text, i) => (
              <span key={i}>{text}</span>
            ))}
          </dd>
          <dd className="tools">
            {site.tools}
          </dd>
        </dl>
      </a>
      <span className="copy">{site.copy}</span>
    </li>
  )
};
export default WorkList;